import "./Gallery.scss"
import React, { Component, lazy, Suspense} from "react";
import { Spinner } from "react-bootstrap";

const GalleryCard = lazy(() => import("../../componentes/GalleryCard/GalleryCard"))
const renderLoader = () => <p>Loading</p>;

class Gallery extends Component {
  state = {
    galleryData: [],
    cargando: true
  }

  componentDidMount() {
    fetch('https://my-json-server.typicode.com/Luis923/json-db/gallery')
      .then(res => res.json())
      .then(galleryData => this.setState({ galleryData, cargando: false }))
      .catch(error => {
        // Manejo del error
      })
  }

  render() {
    if (this.state.cargando) {
      return (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      )
    }

    return (
      <div className="gallery pb-5" id="list-item-4">
        <h2 className="galleryTitle text-center my-3 fs-1 fw-bold">GALLERY</h2>
        <div className="container">
          <div className="row gx-0 gx-sm-5 gy-4">
            <Suspense fallback={renderLoader()}>
            {this.state.galleryData.map(galleryDat => (
              <div className="col-12 col-sm-6 col-md-4 col-lg-3 col-xl-3" key={galleryDat.id}>
                <GalleryCard
                  urlImagen={galleryDat.url}
                  subtitulo={galleryDat.subtitulo}
                  parrafo={galleryDat.parrafo}
                />
              </div>
            ))}
            </Suspense>
          </div>
        </div>
      </div>
    )
  }
}

export default Gallery