import React, { Component } from "react";
import WelcomeImage1 from "../../images/Welcome/welcomeImage1.webp"
import WelcomeImage2 from "../../images/Welcome/welcomeImage2.webp"
import WelcomeImage3 from "../../images/Welcome/welcomeImage3.webp"
import WelcomeImage4 from "../../images/Welcome/welcomeImage4.webp"
import "./Welcome.scss"


class Welcome extends Component {
  render() {
    return (
      <div className="welcome container bg-white py-4" id="list-item-2">
        <div className="row pt-3">
          <div className="col-12 col-xl-6">
            <h2 className="welcomeTitle fs-1">Welcome</h2>
            <p className="welcomeParagraph1 fw-bold	fs-5">Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
            Suspendisse porta erat sit amet eros sagittis, quis hendrerit libero aliquam. Fusce semper 
            augue ac dolor efficitur, a pretium metus pellentesque.</p>
            <p className="welcomeParagraph2 fs-5">Sed posuere laoreet neque, eu rhoncus libero accumsan 
            eget. Morbi tempor nibh ullamcorper urna hendrerit, at molestie tortor molestie. 
            Suspendisse vel porttitor metus, vitae luctus velit. Donec dictum suscipit orci nec 
            facilisis. Sed pretium eu diam id tincidunt.</p>
          </div>
          <div className="col-12 col-xl-6">
            <div className="row g-4">
              <div className="col-12 col-sm-6 ">
                <img className="welcomeImage mx-auto d-block" src={WelcomeImage1} alt="imagen"/>
              </div>
              <div className="col-12 col-sm-6">
                <img className="welcomeImage mx-auto d-block" src={WelcomeImage2} alt="imagen"/>
              </div>
              <div className="col-12 col-sm-6">
                <img className="welcomeImage mx-auto d-block" src={WelcomeImage3} alt="imagen"/>
              </div>
              <div className="col-12 col-sm-6">
                <img className="welcomeImage mx-auto d-block" src={WelcomeImage4} alt="imagen"/>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Welcome