import React,{ lazy, Suspense} from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import reportWebVitals from './reportWebVitals';

import OffcanvasComponent from "./componentes/Offcanvas/OffcanvasComponent";
import Main from "./articles/Main/Main"
import Welcome from "./articles/Welcome/Welcome"
import Servicios from "./articles/Servicios/Servicios"
import Gallery from './articles/Gallery/Gallery';
import OurTeam from './articles/OurTeam/OurTeam';
import News from './articles/News/News';
import PriceTable from './articles/PriceTable/PriceTable';
/* import Contact from "./articles/Contact/Contact"; */
/* import Footer from "./articles/Footer/Footer" */

const Contact = lazy(() => import("./articles/Contact/Contact"))
const Footer = lazy(() => import("./articles/Footer/Footer"))
const renderLoader = () => <p>Loading</p>;

ReactDOM.render(
  <React.StrictMode>
    <OffcanvasComponent/>
    <Main />
    <Welcome />
    <Servicios />
    <Gallery />
    <OurTeam />
    <News />
    <PriceTable />
    <Suspense fallback={renderLoader()}>
    <Contact />
    <Footer />
    </Suspense>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
