import "./News.scss"
import React, { Component, lazy, Suspense} from "react";
import { Spinner } from "react-bootstrap";

const NewsCard = lazy(() => import("../../componentes/NewsCard/NewsCard"))
const renderLoader = () => <p>Loading</p>;

class News extends Component {
  state = {
    newsData: [],
    cargando: true
  }

  componentDidMount() {
    fetch('https://my-json-server.typicode.com/Luis923/json-db/news')
      .then(res => res.json())
      .then(newsData => this.setState({ newsData, cargando: false }))
      .catch(error => {
        // Manejo del error
      })
  }

  render() {
    if (this.state.cargando) {
      return (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      )
    }
    return (
      <div className="News p-4" id="list-item-5">
        <h2 className="NewsTitle text-center fs-1">News</h2>
        <div className="container-fluid">
          <div className="row gx-5">
            <Suspense fallback={renderLoader()}>
            {this.state.newsData.map(newsDat => (
              <div className="col-12 col-sm-6 col-md-3 col-lg-3" key={newsDat.id}>
                <NewsCard
                  urlImagen={newsDat.url}
                  subtitulo={newsDat.subtitulo}
                  subtitulo2={newsDat.subtitulo2}
                  parrafo={newsDat.parrafo}
                  parrafo2={newsDat.parrafo2}
                  color={newsDat.color}
                />
              </div>
            ))}
            </Suspense>
          </div>
        </div>
      </div>
    )
  }
}

export default News