import React, { useState } from "react";
import { Offcanvas, Button } from "react-bootstrap";
import "./OffcanvasComponent.scss"

function OffcanvasComponent() {
    const [show, setShow] = useState(false);
  
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const styleOffcanvasItem ={
      textDecoration: 'none',color: 'white',fontSize: '20px', paddingBottom: '20px'
    }
 
    return (
      <div className="offcanvasComponent">
        <Button className="offcanvasButton fixed-top" variant="primary" onClick={handleShow}>
          Launch
        </Button>
  
        <Offcanvas
          show={show} 
          onHide={handleClose} 
          placement={"end"} 
          name={"end"}
          scroll={"false"}
          className="offcanvasContent" 
          style={{
            width: '220px',
            background: '#e3117c'
          }}
        >
          <Offcanvas.Header closeButton>
            <Offcanvas.Title className="text-white">Offcanvas</Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body >
            <div id="list-example" className="list-group">
              <a href="#list-item-1" style={styleOffcanvasItem}>Home</a>
              <a href="#list-item-2" style={styleOffcanvasItem}>About</a>
              <a href="#list-item-3" style={styleOffcanvasItem}>Services</a>
              <a href="#list-item-4" style={styleOffcanvasItem}>Gallery</a>
              <a href="#list-item-5" style={styleOffcanvasItem}>News</a>
              <a href="#list-item-6" style={styleOffcanvasItem}>Price</a>
              <a href="#list-item-7" style={styleOffcanvasItem}>Contact</a>
            </div>
          </Offcanvas.Body>
        </Offcanvas>
      </div>
    );
}

export default OffcanvasComponent