import "./PriceTable.scss"
import React, { Component, lazy, Suspense} from "react";
import { Spinner } from "react-bootstrap";

const PriceTableCard = lazy(() => import("../../componentes/PriceTableCard/PriceTableCard"))
const renderLoader = () => <p>Loading</p>;

class PriceTable extends Component {
  state = {
    priceTableData: [],
    cargando: true
  }

  componentDidMount() {
    fetch('https://my-json-server.typicode.com/Luis923/json-db/priceTable')
      .then(res => res.json())
      .then(priceTableData => this.setState({ priceTableData, cargando: false }))
      .catch(error => {
        // Manejo del error
      })
  }

  render() {
    if (this.state.cargando) {
      return (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      )
    }
    return (
      <div className="priceTable p-5" id="list-item-6">
        <h2 className="priceTableTitle text-center fs-1">PRICE-TABLE</h2>
        <div className="container-fluid">
          <div className="row d-flex justify-content-center gx-5">
            <Suspense fallback={renderLoader()}>
            {this.state.priceTableData.map((priceTableDat) => (
              <div className="col-12 col-sm-4 mt-2" key={priceTableDat.id}>
                <PriceTableCard
                  urlImagen={priceTableDat.urlImagen}
                  subtitulo={priceTableDat.subtitulo}
                  subtitulo2={priceTableDat.subtitulo2}
                  parrafo={priceTableDat.parrafo}
                  parrafo2={priceTableDat.parrafo2}
                  parrafo3={priceTableDat.parrafo3}
                  parrafo4={priceTableDat.parrafo4}
                  parrafo5={priceTableDat.parrafo5}
                  parrafo6={priceTableDat.parrafo6}
                  color={priceTableDat.color}
                />
              </div>
            ))}
            </Suspense>
          </div>
        </div>
      </div>
    )
  }
}

export default PriceTable