import React, { Component, lazy, Suspense} from "react";
import "./Servicios.scss"
import { Spinner } from "react-bootstrap";

const ServiciosCardIcon = lazy(() => import("../../componentes/ServiciosCards/ServiciosCardIcon"))
const renderLoader = () => <p>Loading</p>;

class Servicios extends Component {
  state = {
    serviciosData: {},
    serviciosIconData: [],
    cargando: true
  }

  componentDidMount() {

    fetch('https://my-json-server.typicode.com/Luis923/json-db/serviciosIcon')
      .then(res => res.json())
      .then(serviciosIconData => this.setState({ serviciosIconData, cargando: false }))
  }

  render() {
    var contador=0

    if (this.state.cargando) {
      return (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      )
    }
    
    return (
      <div className="servicios py-4 bg-dark" id="list-item-3">
        <h3 className="serviciosTitle text-center text-white fs-1">SERVICES</h3>
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-12 col-md-4">
              <div className="row h-100 d-flex align-content-between">
                <Suspense fallback={renderLoader()}>
                {this.state.serviciosIconData.map((serviciosIconDat) => {
                  contador ++
                  if(contador===3){
                    return (
                      <br key={serviciosIconDat.id}></br>
                    )
                  }
                  else  
                    return (
                      <div className="col-12 col-sm-4 col-md-12 my-3" key={serviciosIconDat.id}>
                        <ServiciosCardIcon
                          subtitulo={serviciosIconDat.subtitulo}
                          parrafo={serviciosIconDat.parrafo}
                          color={serviciosIconDat.color}
                          icono="none"
                        />
                      </div>
                    )
                } 
                )}
                </Suspense>
              </div>
            </div>
            <div className="col-12 col-sm-12 col-md-8">
              <div className="row h-100 d-flex align-content-between">
                <Suspense fallback={renderLoader()}>
                {this.state.serviciosIconData.map((serviciosIconDat) => (
                  <div className="col-12 col-sm-6 col-md-6 my-3 my-3" key={serviciosIconDat.id}>
                    <ServiciosCardIcon
                      subtitulo={serviciosIconDat.subtitulo}
                      parrafo={serviciosIconDat.parrafo}
                      color={serviciosIconDat.color}
                      icono={serviciosIconDat.icono}
                    />
                  </div>
                ))}
                </Suspense>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Servicios