import "./OurTeam.scss"
import React, { Component, lazy, Suspense} from "react";
import { Spinner } from "react-bootstrap";

const OurTeamCard = lazy(() => import("../../componentes/OurTeamCard/OurTeamCard"))
const renderLoader = () => <p>Loading</p>;

class OurTeam extends Component {
  state = {
    ourTeamData: [],
    cargando: true
  }

  componentDidMount() {
    fetch('https://my-json-server.typicode.com/Luis923/json-db/ourTeam')
      .then(res => res.json())
      .then(ourTeamData => this.setState({ ourTeamData, cargando: false }))
      .catch(error => {
        // Manejo del error
      })
  }

  render() {
    if (this.state.cargando) {
      return (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      )
    }
    return (
      <div className="ourTeam">
        <h2 className="ourTeamTitle text-center fs-1">OUR TEAM</h2>
        <div className="container">
          <div className="row gx-0 gx-sm-5 gy-4">
            <Suspense fallback={renderLoader()}>
            {this.state.ourTeamData.map((ourTeamDat) => (
              <div className="col-12 col-sm-6 col-md-3 mt-2" key={ourTeamDat.id}>
                <OurTeamCard
                  urlImagen={ourTeamDat.url}
                  subtitulo={ourTeamDat.subtitulo}
                  subtitulo2={ourTeamDat.subtitulo2}
                  parrafo={ourTeamDat.parrafo}
                />
              </div>
            ))}
            </Suspense>
          </div>
        </div>
      </div>
    )
  }
}

export default OurTeam