import React, { Component } from "react";
import { Carousel } from "react-bootstrap";
import mainImage1 from "../../images/Main/mainImage1.webp"
import mainImage2 from "../../images/Main/mainImage2.webp"
import mainImage3 from "../../images/Main/mainImage3.webp"
import mainImage1Movil from "../../images/Main/mainImage1Movil.webp"
import mainImage2Movil from "../../images/Main/mainImage2Movil.webp"
import mainImage3Movil from "../../images/Main/mainImage3Movil.webp"
import "./Main.scss"

class Main extends Component {
  render(){
    return(
      <main className="main container-fluid p-0" id="list-item-1">
        <Carousel className="">
            <Carousel.Item className="" interval={1000}>
              <picture>
                <source media="(min-width: 360px)" srcSet={mainImage1} />
                <img className="carouselImage d-block " src={mainImage1Movil} alt="mainImage1"/>
              </picture>
              <Carousel.Caption className="mainText d-none d-md-block pb-5">
                <h2 className="mainTitle mb-5 pb-5 ps-5 fs-1">WELLNESS</h2>
                <h3 className="mainSubtitle mb-4 pt-5">Welcome</h3>
                <h4 className="mainSubtitle2 mb-4 fs-2">Make a Amazing Look</h4>
                <p className="mb-5 pb-5">Lorem ipsum dolor sit</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item className="" interval={1000}>
              <picture>
                <source media="(min-width: 360px)" srcSet={mainImage2} />
                <img className="carouselImage d-block " src={mainImage2Movil} alt="mainImage1" loading="lazy"/>
              </picture>
              <Carousel.Caption className="mainText d-none d-md-block">
                <h2 className="mainTitle mb-5 pb-5 ps-5 fs-1">WELLNESS</h2>
                <h3 className="mainSubtitle mb-4 pt-5">Welcome</h3>
                <h4 className="mainSubtitle2 mb-4 fs-2">Wellness Body Massage</h4>
                <p className="mb-5 pb-5">Lorem ipsum dolor sit</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item  className="" interval={1000}>
              <picture>
                <source media="(min-width: 360px)" srcSet={mainImage3} />
                <img className="carouselImage d-block " src={mainImage3Movil} alt="mainImage1" loading="lazy"/>
              </picture>
              <Carousel.Caption className="mainText d-none d-md-block">
                <h2 className="mainTitle mb-5 pb-5 ps-5 fs-1">WELLNESS</h2>
                <h3 className="mainSubtitle mb-4 pt-5">Welcome</h3>
                <h4 className="mainSubtitle2 mb-4 fs-2">Best Beauty And Spa</h4>
                <p className="mb-5 pb-5">Lorem ipsum dolor sit</p>
              </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
      </main>
    )
  }
}

export default Main